.section-footer {
    background-color: var(--main-color);
    padding: 60px 0;
}

.contact-me {
    // line-height: 1.27;
    // text-transform: uppercase;
    color: var(--light-color);
    // font-size: 30px;
    // font-weight: bold;
    // text-shadow: 4px 6px 6px rgb(0, 0, 0);
    // text-align: center;
    @include text(4px 6px 6px rgb(0, 0, 0), uppercase, center);
    @include font(bold, 30px, 1.27, null);


    @media screen and (min-width: 320px) and (max-width: 479px) {
        // font-size: 24px;
        @include font(null, 24px, null, null);
    }

}

.section-contacts {
    @media screen and (min-width: 480px) {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }

}

.contact-item,
.footer-messenger {
    margin: 30px 20px;
}

.mobile-link {
    @extend %align-element;
    // display: flex;
    justify-content: center;
    // align-items: center;
    flex-direction: row;
    gap: 100px;
    @media screen and (min-width: 480px) {
        gap: 30px;
        flex-direction: column;
    }
}

.icon {
    // border-radius: 20%;
    padding: 5px;
    background-color: #B38B59;
    // border: 1px solid #9b986f;
    @include border(1px solid #9b986f, 20%);
    box-shadow: 0 2px 6px -1px rgba(51, 51, 51, .23);
    box-shadow: -2px -2px 6px #9b986f, 2px 2px 4px #9b986f;
    margin: 0 10px 0 0;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    fill: var(--main-color);

    &:hover {
        background-color: #9b986f;
        // border: 1px solid #B38B59;
        @include border(1px solid #B38B59, null);
        box-shadow: 0 5px 7px -1px rgba(51, 51, 51, .23);
        box-shadow: -3px -3px 7px #B38B59, 3px 3px 5px #B38B59;
        fill: var(--light-color);
    }
    
}

.contact-icon {
    font-style: normal;
    text-decoration: none;
    // line-height: 1.71;
    @include font(null, null, 1.71, null);
    color: rgba(255, 255, 255, 0.6);
    margin-bottom: 9px;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
    &:hover,
    &:focus {
        color: var(--accent-color);
    }

}

.contact-icon:last-child {
    margin-bottom: 0;
}


.footer-logo {
    // text-align: end;
    @include text(null, null, end);
}