.hero {
    display: flex;
    padding: 45px 0;
    background: linear-gradient(139deg, var(--main-color) 50%, var(--light-color) 50%);
    

    @media screen and (min-width: 320px) and (max-width: 767px) {
        background: var(--main-color);
    }
}

.box-decoration {
    max-width: 100%;
    // width: 500px;
    @include size(500px, null);
    background: rgba(255, 255, 255, 0.068);
    // border: 2px solid rgba(255, 255, 255, 0.174);
    // border-radius: 0.6em;
    @include border(2px solid rgba(255, 255, 255, 0.174), 0.6em);
    flex-direction: column;
    display: flex;
    margin: 0 20px;
    padding: 45px 25px;
    box-shadow: -2px -2px 3px #3a6b4b, 2px 2px 3px rgba(194, 190, 105, 0.361);

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin: 0 10px;
        padding: 15px;
    }

    @media screen and (min-width: 320px) and (max-width: 767px) {
        margin-top: 20px;
    }
}

.hero-section {
    @extend %align-element;
    // display: flex;
    // align-items: center;
    justify-content: center;
    gap: 200px;    
        
    @media screen and (min-width: 768px) and (max-width: 1199px) {
        gap: 50px;
    }

    @media screen and (min-width: 320px) and (max-width: 767px) {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column-reverse;
        gap: 30px;
    }
}


.hero-title {
    // line-height: 1.27;
    // text-transform: uppercase;
    color: var(--light-color);
    // font-size: 30px;
    // text-shadow: 4px 6px 6px rgb(0, 0, 0);
    @include text(4px 6px 6px rgb(0, 0, 0), uppercase, null);
    @include font(null, 30px, 1.27, null);

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        // font-size: 30px;
        @include font(null, 30px, null, null);
    }

}

.hero-details {
    // font-size: 24px;
    @include font(null, 24px, null, null);
    box-shadow: inset -2px -2px 5px rgba(179, 138, 89, 0.589), inset 2px 2px 5px #b38a599c;
    // border-radius: 10px;
    @include border(null, 10px);
    margin: 30px 12px 0 0;
    padding: 12px 34px;
    color: var(--second-color);
    text-shadow: 1px 1px 2px var(--dark-text-color), 0 0 1em var(--dark-text-color);
    max-width: 300px;

    @media screen and (min-width: 320px) and (max-width: 1199px) {
        margin-right: 8px;
        padding: 8px 12px;
    }
}

.my-name {
    position: relative;
    -webkit-box-reflect: below -20px linear-gradient(transparent, rgba(0, 0, 0, .2));
    display: inline-block;
}

.my-name span {
    position: relative;
    font-family: 'Lobster', sans-serif;
    display: inherit;
    // font-size: 40px;
    @include font(null, 40px, null, null);
    color: var(--accent-color);
    @include animation(my-name 3s, infinite, calc(.1s * var(--i)));
}

