.hero-img {
    @extend %align-element;
    // display: flex;
    justify-content: center;
    // align-items: center;
    flex-direction: column;
    margin: 0 30px;
    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin: 0 15px;
    }
}

.photo {
    // border-radius: 50%;
    @include border(null, 50%);
    @include size(245px, 285px);
    // height: 285px;
    // width: 245px;
    box-shadow: -4px -4px 15px var(--main-color), 5px 5px 15px var(--main-color);
    @media screen and (min-width: 768px) and (max-width: 1199px) {
        // height: 250px;
        // width: 250px;
        @include size(250px, 250px);
        box-shadow: -4px -4px 15px var(--main-color), 5px 5px 15px var(--main-color);
    }

    @media screen and (min-width: 320px) and (max-width: 767px) {
        // height: 255px;
        // width: 220px;
        @include size(220px, 255px);
        box-shadow: -2px -2px 10px var(--light-color), 2px 2px 10px var(--light-color);
    }
}

.link-cv {
    background: var(--main-color);
    @include size(150px, 40px);
    // height: 40px;
    // width: 150px;
    // border-radius: 60px;
    // border: 2px solid var(--decoration-color);
    @include border(2px solid var(--decoration-color), 60px);
    transition: all linear 0.3s;
    position: relative;
    top: 20px;
    // font-weight: bold;
    @include font(bold, null, null, null);
    color: var(--dark-text-color);
    text-shadow: 1px 1px 1px var(--light-color), 0 0 1em var(--light-color);
    @extend %align-element;
    // display: flex;
    // align-items: center;
    justify-content: center;

    &:hover {
        transform: scale(1.1);
        background: var(--light-color);
        // border: 2px solid var(--main-color);
        @include border(2px solid var(--main-color), null);
    }

    @media screen and (min-width: 320px) and (max-width: 767px) {
        background: var(--main-color);
        // border: 2px solid var(--accent-color);
        @include border(2px solid var(--accent-color), null);

        &:hover {
            transform: scale(1.1);
            background: var(--accent-color);
            // border: 2px solid var(--light-color);
            @include border(2px solid var(--light-color), null);
        }
    }

}