:root {
  --main-color: #113e21;
  --accent-color: #b38b59;
  --dark-text-color: #131313;
  --light-color: #f1f1f1;
  --second-color: #ababab;
  --decoration-color: #7a7a7a;
}

.mobile-link, .project-skill, .projects-title, .skill-description-text, .skill-item, .skill-list, .skill-title, .title-name, .link-cv, .hero-img, .hero-section, .burger-head, .menu, .nav {
  align-items: center;
  display: flex;
}

@keyframes my-name {
  0%, 40%, 100% {
    transform: translateY(0);
  }

  20% {
    transform: translateY(-10px);
  }
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-y: scroll;
}

html, body {
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, ul, p {
  margin: 0;
  padding: 0;
}

ul, a {
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

body {
  letter-spacing: .04em;
  width: 100vw;
  margin: 0;
  font-family: Eb Garamond;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.27;
}

button {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: inherit;
}

.no-scroll {
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 45px;
    padding-right: 45px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .container {
    max-width: 768px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

section {
  align-content: center;
  align-items: center;
  justify-items: center;
  display: grid;
}

.hidden {
  opacity: 0;
  filter: blur(5px);
  transition: all 1.2s;
  transform: translateX(-100%);
}

.show {
  opacity: 1;
  filter: blur();
  transform: translateX(0);
}

.header {
  background-color: var(--main-color);
  border-bottom: 2px solid rgba(96, 101, 99, .65);
}

.nav {
  height: 60px;
  justify-content: space-between;
}

.drawer {
  position: relative;
  overflow: hidden;
}

.logo-text {
  color: #b38b59;
  text-shadow: 2px 2px 2px var(--decoration-color);
  font-family: Lobster;
  font-size: 36px;
}

.menu {
  justify-content: center;
  gap: 30px;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.25;
}

@media screen and (max-width: 767px) {
  .menu {
    display: none;
  }
}

.menu-link {
  color: var(--light-color);
  text-shadow: 4px 6px 6px #000;
  transition: all .3s;
}

.menu-link:hover, .menu-link:focus {
  color: var(--accent-color);
  text-shadow: 4px 6px 6px #000;
}

.burger-btn {
  background-color: var(--main-color);
  cursor: pointer;
  fill: #b38b59;
  border-width: 0;
  margin-left: auto;
  position: relative;
}

.burger-btn:hover {
  transform: scale(1.1);
}

@media screen and (min-width: 768px) {
  .burger-btn {
    display: none;
  }
}

.burger-menu {
  z-index: 20;
  -webkit-backdrop-filter: blur(40px);
  backdrop-filter: blur(40px);
  width: 80%;
  height: 100%;
  transition: all .3s linear;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
}

.burger-menu:hover {
  width: 2%;
}

@media screen and (min-width: 767px) {
  .burger-menu {
    display: none;
  }
}

.is-hidden {
  display: block;
}

.burger-head {
  justify-content: flex-end;
  margin: 15px 0;
}

.burger-modal-close-btn {
  fill: #b38b59;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.burger-list {
  text-align: center;
  margin-top: 50px;
}

.burger-item {
  padding-bottom: 20px;
  font-size: 30px;
  font-weight: 600;
  transition: all .2s linear;
}

.burger-item:last-child {
  padding: 0;
}

.hero {
  background: linear-gradient(139deg, var(--main-color) 50%, var(--light-color) 50%);
  padding: 45px 0;
  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .hero {
    background: var(--main-color);
  }
}

.box-decoration {
  max-width: 100%;
  width: 500px;
  background: rgba(255, 255, 255, .067);
  border: 2px solid rgba(255, 255, 255, .173);
  border-radius: .6em;
  flex-direction: column;
  margin: 0 20px;
  padding: 45px 25px;
  display: flex;
  box-shadow: -2px -2px 3px #3a6b4b, 2px 2px 3px rgba(194, 190, 105, .36);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .box-decoration {
    margin: 0 10px;
    padding: 15px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .box-decoration {
    margin-top: 20px;
  }
}

.hero-section {
  justify-content: center;
  gap: 200px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .hero-section {
    gap: 50px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .hero-section {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    gap: 30px;
    display: flex;
  }
}

.hero-title {
  color: var(--light-color);
  text-shadow: 4px 6px 6px #000;
  text-transform: uppercase;
  font-size: 30px;
  line-height: 1.27;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .hero-title {
    font-size: 30px;
  }
}

.hero-details {
  color: var(--second-color);
  text-shadow: 1px 1px 2px var(--dark-text-color), 0 0 1em var(--dark-text-color);
  max-width: 300px;
  border-radius: 10px;
  margin: 30px 12px 0 0;
  padding: 12px 34px;
  font-size: 24px;
  box-shadow: inset -2px -2px 5px rgba(179, 138, 89, .59), inset 2px 2px 5px rgba(179, 138, 89, .61);
}

@media screen and (min-width: 320px) and (max-width: 1199px) {
  .hero-details {
    margin-right: 8px;
    padding: 8px 12px;
  }
}

.my-name {
  -webkit-box-reflect: below -20px linear-gradient(transparent, rgba(0, 0, 0, .2));
  display: inline-block;
  position: relative;
}

.my-name span {
  display: inherit;
  color: var(--accent-color);
  animation: my-name 3s infinite;
  animation-delay: calc(.1s * var(--i) );
  font-family: Lobster, sans-serif;
  font-size: 40px;
  position: relative;
}

.hero-img {
  flex-direction: column;
  justify-content: center;
  margin: 0 30px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .hero-img {
    margin: 0 15px;
  }
}

.photo {
  width: 245px;
  height: 285px;
  box-shadow: -4px -4px 15px var(--main-color), 5px 5px 15px var(--main-color);
  border-radius: 50%;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .photo {
    width: 250px;
    height: 250px;
    box-shadow: -4px -4px 15px var(--main-color), 5px 5px 15px var(--main-color);
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .photo {
    width: 220px;
    height: 255px;
    box-shadow: -2px -2px 10px var(--light-color), 2px 2px 10px var(--light-color);
  }
}

.link-cv {
  background: var(--main-color);
  width: 150px;
  height: 40px;
  border: 2px solid var(--decoration-color);
  color: var(--dark-text-color);
  text-shadow: 1px 1px 1px var(--light-color), 0 0 1em var(--light-color);
  border-radius: 60px;
  justify-content: center;
  font-weight: bold;
  transition: all .3s linear;
  position: relative;
  top: 20px;
}

.link-cv:hover {
  background: var(--light-color);
  border: 2px solid var(--main-color);
  transform: scale(1.1);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .link-cv {
    background: var(--main-color);
    border: 2px solid var(--accent-color);
  }

  .link-cv:hover {
    background: var(--accent-color);
    border: 2px solid var(--light-color);
    transform: scale(1.1);
  }
}

.about-section {
  background: var(--light-color);
  padding-bottom: 40px;
}

.title-name {
  color: var(--main-color);
  text-shadow: 3px 3px 5px #000;
  text-transform: uppercase;
  gap: 30px;
  margin-left: 10%;
  padding: 40px 0;
  font-size: 30px;
}

.title-name:before {
  content: "";
  width: 80px;
  height: 2px;
  background-color: var(--main-color);
  transform: translateY(100%);
  box-shadow: 2px 2px 5px #000;
}

@media screen and (max-width: 767px) {
  .title-name {
    gap: 20px;
    font-size: 24px;
  }
}

.about-style {
  justify-content: center;
  align-items: baseline;
  gap: 70px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .about-style {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }
}

.about-text {
  width: 50%;
  background: rgba(17, 62, 33, .455);
  border: 2px solid rgba(17, 62, 33, .443);
  border-radius: .5em;
  padding: 10px 25px;
  font-size: 20px;
  font-weight: bold;
}

@media screen and (max-width: 767px) {
  .about-text {
    width: 90%;
  }
}

.about-description {
  width: 50%;
  font-size: 20px;
}

@media screen and (max-width: 767px) {
  .about-description {
    width: 90%;
  }
}

.about-info .tab-content {
  padding: 20px 0;
  display: none;
}

.about-info .tab-content.active {
  display: block;
}

.about-tabs .tab-item {
  opacity: .7;
  cursor: pointer;
  color: var(--main-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0 30px 0 0;
  padding: 2px 0;
  font-weight: 600;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.about-tabs .tab-item:last-child {
  margin: 0;
}

.about-tabs .tab-item:before {
  content: "";
  width: 0;
  height: 1px;
  background-color: var(--main-color);
  transition: width .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.about-tabs .tab-item.active {
  color: var(--main-color);
  opacity: 1;
  cursor: auto;
}

.about-tabs .tab-item.active:before {
  width: 100%;
  background-color: var(--main-color);
}

.about-info .timeline {
  position: relative;
}

.about-info .timeline:before {
  content: "";
  height: 100%;
  width: 1px;
  background-color: var(--accent-color);
  position: absolute;
  top: 0;
  left: 5px;
}

.about-info .timeline-item {
  margin-bottom: 15px;
  padding: 10px 0 0 40px;
  position: relative;
}

.about-info .timeline-item:before {
  content: "";
  width: 11px;
  height: 11px;
  background-color: var(--accent-color);
  border-radius: 50%;
  position: absolute;
  top: 16px;
  left: 0;
}

.about-info .timeline-item:last-child {
  margin-bottom: 0;
}

.date {
  border: 2px solid var(--accent-color);
  background-color: rgba(179, 138, 89, .373);
  border-radius: 20px;
}

.about-info .timeline-item .date {
  margin: 0 0 10px;
  padding: 5px;
  font-weight: bold;
  display: inline-block;
}

.about-info .timeline-item h4 {
  margin: 0 0 10px;
  font-size: 18px;
}

.about-info .timeline-item h4 span {
  font-weight: 400;
}

.section-skills {
  background: rgba(182, 179, 179, .635);
  padding-bottom: 80px;
}

.skill-title {
  color: var(--main-color);
  text-shadow: 3px 3px 5px #000;
  text-transform: uppercase;
  flex-direction: row-reverse;
  gap: 30px;
  margin-right: 12%;
  padding: 50px 0;
  font-size: 30px;
}

.skill-title:after {
  content: "";
  width: 80px;
  height: 2px;
  background-color: var(--main-color);
  transform: translateY(100%);
  box-shadow: 2px 2px 5px #000;
}

@media screen and (max-width: 767px) {
  .skill-title {
    gap: 20px;
    font-size: 24px;
  }
}

.skill-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.skill-list:hover > :not(:hover) {
  opacity: .4;
  background-color: var(--main-color);
  transform: scale(.9);
  box-shadow: -3px -3px 7px #777676, 3px 3px 5px #9e9e9e;
}

.skill-item {
  background-color: var(--accent-color);
  width: 240px;
  height: 240px;
  cursor: pointer;
  border-radius: 50%;
  flex-direction: row;
  justify-content: center;
  transition: all .35s;
  position: relative;
  overflow: hidden;
  box-shadow: inset -3px -3px 7px #76546d, inset 3px 3px 5px #af9898;
}

.skill-item:hover .skill-description-text {
  transform: translateY(0);
}

.skill-item img {
  width: 80px;
  height: 80px;
  display: inline;
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .skill-item img {
    width: 70px;
    height: 70px;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .skill-item {
    width: calc(45% - 10px);
  }
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .skill-item {
    width: calc(100% - 80px);
  }
}

.skill-description-text {
  background: var(--light-color);
  height: calc(100% + 1px);
  width: inherit;
  box-shadow: -3px -3px 7px var(--decoration-color);
  justify-content: center;
  padding: 15px;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  overflow: auto;
  transform: translateY(100%);
}

.skill-name {
  color: var(--light-color);
  text-shadow: 2px 2px 3px #000;
  font-family: Lobster, sans-serif;
  font-size: 26px;
}

.skill-description {
  text-align: center;
}

.skill-item:nth-child(1) {
  transition-delay: .2s;
}

.skill-item:nth-child(2) {
  transition-delay: .5s;
}

.skill-item:nth-child(3) {
  transition-delay: .8s;
}

.projects-section {
  background: var(--light-color);
}

.projects-title {
  justify-content: center;
}

.slider {
  width: 700px;
  height: 680px;
  z-index: 2;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .slider {
    max-width: 400px;
    height: 550px;
  }
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .slider {
    width: 280px;
    height: 530px;
  }
}

.slider-project {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity .7s;
  position: absolute;
  top: 0;
  left: 7%;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .slider-project {
    left: 0;
  }
}

.slider-project.active {
  opacity: 1;
}

.slider img {
  z-index: 1;
  border-radius: 20px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slider .prev {
  filter: invert(60%);
  height: 40px;
  left: 5px;
}

.slider .prev:hover, .slider .prev:focus {
  border: 2px solid var(--accent-color);
  background-color: var(--accent-color);
  filter: invert(10%);
  box-shadow: 2px 2px 10px #000;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .slider .prev {
    height: 20px;
    left: 1px;
  }

  .slider .prev:hover, .slider .prev:focus {
    border: 2px solid var(--accent-color);
    background-color: var(--accent-color);
    filter: invert(10%);
    box-shadow: 2px 2px 10px #000;
  }
}

.slider .next {
  filter: invert(60%);
  height: 40px;
  right: 5px;
}

.slider .next:hover, .slider .next:focus {
  border: 2px solid var(--accent-color);
  background-color: var(--accent-color);
  filter: invert(10%);
  box-shadow: 2px 2px 10px #000;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .slider .next {
    height: 20px;
    right: 1px;
  }

  .slider .next:hover, .slider .next:focus {
    border: 2px solid var(--accent-color);
    background-color: var(--accent-color);
    filter: invert(10%);
    box-shadow: 2px 2px 10px #000;
  }
}

.project-info {
  margin: 30px 20px;
}

.project-name {
  color: var(--main-color);
  font-family: Lobster, sans-serif;
  font-size: 30px;
  text-decoration: underline;
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .project-name {
    font-size: 24px;
  }
}

.project-skill-list {
  padding: 5px;
}

.project-skill-item {
  border: 2px solid var(--accent-color);
  background-color: #d4c5b3;
  border-radius: 20px;
  margin: 5px 8px;
  padding: 5px 8px;
}

.project-skill {
  flex-wrap: wrap;
  justify-content: flex-start;
}

.pet-projects-item img {
  border: 3px solid var(--decoration-color);
  border-radius: 25px;
  padding: 15px;
  box-shadow: inset -4px -4px 8px rgba(179, 138, 89, .59), inset 4px 4px 8px rgba(179, 138, 89, .61);
}

.pet-projects-item img:hover, .pet-projects-item img:focus {
  border: 3px solid var(--accent-color);
  box-shadow: -4px -4px 8px rgba(179, 138, 89, .59), 4px 4px 8px rgba(179, 138, 89, .61);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .pet-projects-item img {
    box-shadow: none;
    border: none;
    padding: 25px;
  }

  .pet-projects-item img:hover, .pet-projects-item img:focus {
    box-shadow: none;
    border: none;
  }
}

.project-picture {
  border-radius: 20px;
}

.section-footer {
  background-color: var(--main-color);
  padding: 60px 0;
}

.contact-me {
  color: var(--light-color);
  text-shadow: 4px 6px 6px #000;
  text-transform: uppercase;
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  line-height: 1.27;
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .contact-me {
    font-size: 24px;
  }
}

@media screen and (min-width: 480px) {
  .section-contacts {
    justify-content: space-evenly;
    align-items: center;
    display: flex;
  }
}

.contact-item, .footer-messenger {
  margin: 30px 20px;
}

.mobile-link {
  flex-direction: row;
  justify-content: center;
  gap: 100px;
}

@media screen and (min-width: 480px) {
  .mobile-link {
    flex-direction: column;
    gap: 30px;
  }
}

.icon {
  fill: var(--main-color);
  background-color: #b38b59;
  border: 1px solid #9b986f;
  border-radius: 20%;
  margin: 0 10px 0 0;
  padding: 5px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: -2px -2px 6px #9b986f, 2px 2px 4px #9b986f;
}

.icon:hover {
  fill: var(--light-color);
  background-color: #9b986f;
  border: 1px solid #b38b59;
  box-shadow: -3px -3px 7px #b38b59, 3px 3px 5px #b38b59;
}

.contact-icon {
  color: rgba(255, 255, 255, .6);
  margin-bottom: 9px;
  font-style: normal;
  line-height: 1.71;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.contact-icon:hover, .contact-icon:focus {
  color: var(--accent-color);
}

.contact-icon:last-child {
  margin-bottom: 0;
}

.footer-logo:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
